<template>
  <div id="food">
  	<div class="food" v-show="showFlag" ref="food">
  		<div class="food-content">
  			<div class="image-header">
  				<img :src="food.image" class="img"/>
  				<div class="back" @click="hide">
  					<i class="icon-arrow_lift"></i>
  				</div>
  			</div>
  			<div class="content">
  				<h1 class="name">{{food.name}}</h1>
  				<div class="detail">
  					<span class="sell-count">月售{{food.sellCount}}份</span>
  					<span class="rating">好评率{{food.rating}}%</span>
  				</div>
  				<div class="price">
  					<span class="now">￥{{food.price}}</span>
			    	<span v-show="food.oldPrice" class="old">￥{{food.oldPrice}}</span>
  				</div>
  				<div class="cartcontrol-wrapper">
	  				<cartcontrol @add="addFood" :food="food"></cartcontrol>
	  			</div>
	  			<div class="buy" @click="addFirst($event)" v-show="!food.count || food.count===0">加入购物车</div>
  			</div>
  			<split v-show="food.info"></split>
  			<div class="info" v-show="food.info">
  				<h1 class="title">商品信息</h1>
  				<p class="text">{{food.info}}</p>
  			</div>
  			<split></split>
  			<div class="rating">
  				<h1 class="title">商品评价</h1>
  				<ratingselect @select="selectRating" @toggle="toggleContent" :selectType="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
  				<div class="rating-wrapper">
  					<ul v-show="food.ratings && food.ratings.length">
  						<li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item">
  							<div class="user">
  								<span class="name">{{rating.username}}</span>
  								<img  class="avatar" width="12" height="12" :src="rating.avatar"/>
  							</div>
  							<div class="time">{{rating.rateTime | data}}</div>
  							<p class="text">
  								<span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
  							</p>
  						</li>
  					</ul>
  					<div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
  				</div>
  			</div>
  		</div>
  	</div>
  </div>
</template>
<script type="text/javascript">
import BScroll from 'better-scroll'
import cartcontrol from './cartcontrol.vue'
import split from './split.vue'
import ratingselect from './ratingselect.vue'

const POSITIVE=0;
const NEGATIVE=1;
const ALL=2;

	export default{
		props:{
			food:{
				type:Object
			}
		},
		data(){
			return{
				showFlag:false,
				selectType:ALL,
				onlyContent:true,
				desc:{
					all:'全部',
					positive:'推荐',
					negative:'吐槽'
				}
			}
		},
		methods:{
			show(){
				this.showFlag=true;
				this.selectType=ALL;
				this.onlyContent=true;
				
				this.$nextTick(()=>{
  				if(!this.scroll){
	  				this.scroll=new BScroll(this.$refs.food, {
	            click: true
	        	});
	        }else{
		  			this.scroll.refresh();
		  		}
  			})
			},
			hide(){
				this.showFlag=false;
			},
			addFirst(event){
				if(!event._constructed){
					return
				}
				this.$set(this.food,'count',1)
				
				this.$emit('add','target')
			},
			addFood(target){
				this.$emit('add',target)
			},
			needShow(type,text){
				if(this.onlyContent && !text)
				{
					return false
				}
				if(this.selectType===ALL){
					return true
				}else{
					return type===this.selectType;
				}
			},
			selectRating(type){
				this.selectType = type;
				this.$nextTick(()=>{
  				this.scroll.refresh();
  			})
			},
			toggleContent(only){
				this.onlyContent = !this.onlyContent;
				this.$nextTick(()=>{
  				this.scroll.refresh();
  			})
			}
		},
		components:{
			cartcontrol,
			split,
			ratingselect
		}
	}
</script>
<style type="text/css">
	.food{
		position: fixed;
		top: 0;
		left: 0;
		bottom: 48px;
		z-index: 30;
		width: 100%;
		background: #fff;
	}
	.image-header{
		position: relative;
		width: 100%;
		height: 0;
		padding-top: 100%;
	}
	.image-header .img{
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
	}
	.back{
		position: absolute;
		top: 10px;
		left: 0;
	}
	.icon-arrow_lift{
		font-weight: 700;
		font-size: 20px;
		color: #fff;
		padding: 10px;
		display: block;
	}
	.food .content{
		padding: 18px;
		position: relative;
	}
	.food .title{
		line-height: 14px;
		margin-bottom: 8px;
		font-size: 14px;
		font-weight: 700;
		color: rgb(7,17,27);
	}
	.food .detail{
		margin-bottom: 18px;
		line-height: 10px;
		font-size: 0;
		height: 10px;
	}
	.sell-count,.rating{
		font-size: 10px;
		color: rgb(147,153,159);
	}
	.sell-count{
		margin-right: 12px;
	}
	h1.name{
		font-weight: bold;
    font-size: 16px;
    height: 30px;
    line-height: 30px;
	}
	.price{
		font-weight: 700;
		line-height: 24px;
	}
	.price .now{
		margin-right: 8px;
		font-size: 14px;
		color: rgb(240,20,20);
	}
	.price .old{
		text-decoration: line-through;
		font-size: 10px;
		color: rgb(147,153,159);
	}
	.cartcontrol-wrapper{
		position: absolute;
		right: 12px;
		bottom: 12px;
	}
	.buy{
		position: absolute;
		right: 18px;
		bottom: 18px;
		z-index: 10;
		line-height: 24px;
		height: 24px;
		padding: 0 12px;
		box-sizing: border-box;
		font-size: 10px;
		border-radius: 12px;
		color: #fff;
		background: rgb(0,160,220);
	}
	.info{
		padding: 18px;
	}
	h1.title{
		line-height: 14px;
		margin-bottom: 6px;
		font-size: 14px;
		color: rgb(77,17,27);
	}
	.food .info .text{
		font-size: 12px;
		line-height: 24px;
		padding: 0 8px;
		color: rgb(77,85,93);
	}
	.rating{
		padding-top: 18px;
	}
	.rating .title{
		line-height: 14px;
		margin-left: 18px;
		font-size: 14px;
		color: rgb(7,17,27);
	}
	.rating-wrapper{
		padding: 0 18px;
	}
	.rating-item{
		position: relative;
		padding: 16px 0;
		border-bottom: 1px solid rgba(7,17,27,0.1);
	}
	.user{
		position: absolute;
		right: 0;
		top: 16px;
		line-height: 12px;
		font-size: 0;
	}
	.rating-item .name{
		display: inline-block;
		vertical-align: top;
		font-size: 10px;
		color: rgb(147,153,159);
		margin-right: 6px;
	}
	.rating-item .avatar{
		border-radius: 50%;
	}
	.time{
		margin-bottom: 6px;
		line-height: 12px;
		font-size: 10px;
		color: rgb(147,153,159);
	}
	.food .info .text{
		line-height: 16px;
		font-size: 12px;
		color: rgb(7,17,27);
	}
	.icon-thumb_up,.icon-thumb_down{
		margin-right: 4px;
		line-height: 24px;
		font-size: 12px;
	}
	.icon-thumb_up{
		color: rgb(0,160,220);
	}
	.icon-thumb_down{
		color: rgb(147,153,159);
	}
	.no-rating{
		padding: 16px 0;
		font-size: 12px;
		color: rgb(147,153,159);
	}
</style>


